@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
@import url(https://use.fontawesome.com/releases/v5.7.0/css/all.css);
@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  transform-style: perserve-3d;
  overflow-x: hidden;
  width: 100%;
}

h1,
h2,
h3,
h4 {
  font-weight: 700;
}

p {
  font-size: 5;
}

a,
a:hover {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

.slideanim {
  visibility: hidden;
}

.slide {
  animation-name: slide;
  -webkit-animation-name: slide;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  visibility: visible;
}

button,
.button:focus {
  background-color: transparent;
  border: none;
  outline: none;
}

.button span,
.button::before {
  background: #29323c;
  background: linear-gradient(to right, #6827ff 0%, #6827ffd5 100%);
}

.button {
  display: inline-block;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
}

.button::before {
  content: "";
  display: inline-block;
  height: 2.5rem;
  position: absolute;
  bottom: 0.3125rem;
  left: 1.875rem;
  right: 1.875rem;
  z-index: -1;
  border-radius: 30rem;
  -webkit-filter: blur(1.25rem) brightness(0.95);
          filter: blur(1.25rem) brightness(0.95);
  transform-style: preserve-3d;
  transition: all 0.3s ease-out;
}

.button span {
  display: inline-block;
  padding: 1.125rem 3.75rem;
  border-radius: 50rem;
  position: relative;
  z-index: 2;
  will-change: transform, filter;
  transform-style: preserve-3d;
  transition: all 0.3s ease-out;
}

.button:hover span {
  -webkit-filter: brightness(1.05) contrast(1.05);
          filter: brightness(1.05) contrast(1.05);
  transform: scale(0.95);
}

.button:hover::before {
  bottom: 0;
  -webkit-filter: blur(0.625rem) brightness(0.95);
          filter: blur(0.625rem) brightness(0.95);
}

.button:focus,
.button:active,
.button:hover {
  color: #fff;
}

.link {
  color: #fff;
  position: relative;
  text-decoration: none;
  transition: color 0.4s ease-out;
}

.link:hover {
  color: #eee;
  text-decoration: none;
  right: 0;
}

.link:hover:after {
  border-color: #eee;
  right: 0;
}

.link:after {
  border-radius: 1em;
  border-top: 0.1em solid #ddd;
  content: "";
  position: absolute;
  right: 100%;
  bottom: 0.14em;
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}

.link:hover:after {
  -webkit-animation: anchor-underline 2s cubic-bezier(0, 0.5, 0, 1) infinite;
          animation: anchor-underline 2s cubic-bezier(0, 0.5, 0, 1) infinite;
  border-color: #eee;
}

@keyframes anchor-underline {
  0%,
  10% {
    left: 0;
    right: 100%;
  }

  40%,
  60% {
    left: 0;
    right: 0;
  }

  90%,
  100% {
    left: 100%;
    right: 0;
  }
}

@-webkit-keyframes anchor-underline {
  0%,
  10% {
    left: 0;
    right: 100%;
  }

  40%,
  60% {
    left: 0;
    right: 0;
  }

  90%,
  100% {
    left: 100%;
    right: 0;
  }
}

@keyframes slide {
  0% {
    opacity: 0;
    transform: translateY(70%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@-webkit-keyframes slide {
  0% {
    opacity: 0;
    -webkit-transform: translateY(70%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
  }
}

@media only screen and (max-width: 992px) {
  #navbar-custom {
    background-color: #000;
  }
}

@media only screen and (min-width: 992px) {
  #navbar-custom {
    background-color: #fff;
  }
}

#navbar-custom .navbar-brand,
#navbar-custom .navbar-brand:hover {
  color: #000;
}

#navbar-custom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease-out;
  z-index: 10;
}

@media only screen and (max-width: 992px) {
  #nav-item .nav-link {
    color: #fff;
  }
}

@media only screen and (min-width: 992px) {
  #nav-item .nav-link {
    color: #000;
  }
}

#nav-item {
  padding: 0.3rem 3px;
}

#nav-item .nav-link {
  font-weight: 500;
  font-size: 1rem;
  padding-left: 20px;
  padding-right: 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

#nav-item .nav-btn {
  border-radius: 5px;
  color: #fff;
  background: #6927ff;
}

.header {
  position: relative;
  text-align: center;
  background-image: linear-gradient(
    47deg,
    #8c3cee 0%,
    #2f85f6 60%,
    #2daef7 100%
  );
  color: #fff;
}

.header-img {
  height: 500px;
  -webkit-animation-name: floating;
          animation-name: floating;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  margin-left: 30px;
  margin-top: 5px;
}

.waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px;
  min-height: 100px;
  max-height: 150px;
}

.parallax > path {
  -webkit-animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
          animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}

.parallax > path:nth-child(1) {
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}

.parallax > path:nth-child(2) {
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
  -webkit-animation-duration: 15s;
          animation-duration: 15s;
}

.parallax > path:nth-child(3) {
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
}

.parallax > path:nth-child(4) {
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  -webkit-animation-duration: 25s;
          animation-duration: 25s;
}

@-webkit-keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

@-webkit-keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 15px);
  }
  100% {
    transform: translate(0, -0px);
  }
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 15px);
  }
  100% {
    transform: translate(0, -0px);
  }
}

.footer {
  position: relative;
  text-align: center;
  background: #343434;
  color: #fff;
  font-size: 1.2rem;
}

.login-wrap {
  padding: 4rem;
  margin: 8rem auto;
  width: 60%;
  min-width: 450px;
  border-radius: 10px;
  background: #fff;
  color: #000;
}

.input {
  outline: none;
  border: none;
  font-size: 15px;
  background-color: #e6e6e6;
  color: #666;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  padding: 0 30px 0 68px;
}

.input-icon i {
  position: absolute;
  font-size: 15px;
  color: #666;
  padding-top: 18px;
  padding-left: 35px;
}

.alert-required::before {
  content: "!";
  position: absolute;
  background-color: white;
  border: 1px solid #c80000;
  border-radius: 13px;
  padding: 4px 12px 4px 12px;
  margin-top: 10px;
  right: 40px;
  pointer-events: none;
  color: #c80000;
  font-size: 13px;
  line-height: 1.4;
  text-align: center;
  -webkit-animation: zoomIn 0.2s forwards;
          animation: zoomIn 0.2s forwards;
}

.alert-required:hover::before {
  content: attr(data-validate);
  max-width: 80%;
  -webkit-animation: widthAnimation 0.1s ease-out 0s 1 forwards;
          animation: widthAnimation 0.1s ease-out 0s 1 forwards;
}

@keyframes widthAnimation {
  0% {
    transform: scaleX(0.1);
  }
  100% {
    transform: scaleX(1);
  }
}

@-webkit-keyframes widthAnimation {
  0% {
    transform: scaleX(0.1);
  }
  100% {
    transform: scaleX(1);
  }
}

